<template>
    <div class="tier">
        <PokemonView :pokemon='getSelectedPokemon'></PokemonView>
    </div>
</template>


<script>

import PokemonView from "@/components/_Pokemon.vue"
import axios from "axios"

export default {
    components:{
        PokemonView
    },
    created(){
        const tier = this.$route.params.tier 
        const pkmn = this.$route.params.pokemon

        axios.post("/pokemon", {pokemon: pkmn, tier: tier})
            .then(res => {
                this.$store.commit("set_selectedPokemon", res.data.pokemon)
            })

    }
}
</script>